html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

@font-face {
  font-family: 'Cinzel';
  font-weight: 400;
  font-style: normal;
  font-display: auto;
  unicode-range: U+000-5FF; /* Download only latin glyphs */
  src: local('Cinzel'), url('fonts/Cinzel-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Lato';
  font-weight: 400;
  font-style: normal;
  font-display: auto;
  unicode-range: U+000-5FF; /* Download only latin glyphs */
  src: local('Lato'), url('fonts/Lato-Regular.ttf') format('truetype');
}

body {
  font-family: 'Cinzel', serif;
  margin: 0;
  background-image: linear-gradient(260deg, white 0%, whitesmoke 100%);
}

#wrapper {
  max-width: 1000px;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
}

.masthead a {
  color: whitesmoke;
  font-size: 1.2em;
  font-weight: 100;
  letter-spacing: 2px;
  text-decoration: none;
  background: rgba(0, 0, 0, 0.4);
  padding: 20px 5px;
  display: inline-block;
  width: 100%;
  text-align: center;
  transition: all 0.5s;
}

.masthead a:hover {
  background: rgba(0, 0, 0, 0.3);
}

#controls {
  margin-bottom: 20px;

  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  background: none;
  border: none;
  filter: invert(100%);
}

label {
  font-size: 1.5em;
  flex: 0 0 70px;
}

#pause {
  display: none;
}

section,
footer {
  text-align: center;
  background: rgba(0, 0, 0, 0.4);
  padding: 20px;
  color: white;
  font-weight: 100;
}

footer {
  margin: 20px 0;
}

footer a {
  color: whitesmoke;
  font-size: 1em;
}
